<template>
	<div ref="trys" style="height: 100%">
   	  <div  class="all-container">
   	  	<div class="operate-div">
            <template v-for="(item, $index) in formList">
                <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" @getInput="getFrom"></inputs>
                <combine   v-if="item.type == 'combineSelect'" @getSelects="getOperator" :option="item.options"  width="20em" ></combine>
                <datePicker ref="timer" style="margin-left:10px" v-if="item.type == 'timer'"    type= "doubleDate"  :dateOpt="item.options"
                       @getDatePicker="getFrom"></datePicker>
            </template>
         </div>
         <div ref="myOwnTable" class="table-container" :style="{height: tableHeight || 'calc(100% - 4.5em)'}">
            <tables ref="myTable" :tableOpt="tableOpt" class="tablePadding" v-if="tableShow"
                    @rowDbClick=""></tables>
         </div>
   	  </div>
   </div>
</template>

<script>
   import combine from  '../../../component/childComponent/CombineSelect.vue'
   export default{
     data() {
       return {
         searchParam: {
           	userId: '',
            content: '',
            startDate: '',
            endDate: ''
         },
         tableOpt: {
            column: [
              {title: '操作时间', field: 'createTime',textAlign: 'center',width: '130'},
              {title: '操作人员', field: 'userName', textAlign: 'center', width: '130'},
              {title: '操作内容', field: 'optContent', textAlign: 'left', width: '700'}
            ],
            url: 'comprehensive/queryCustomerLog',
            method: 'post',
            // method: 'get',
            // url: 'dist/json/operateLog.json',
            pageSize: 20,
            pageNumber: 1,
            pageList: [20, 40, 60],
            queryParam: {}
           // queryType: 'form'
        },
        tableShow: false,
        tableHeight: '',
        searchDate: ''
       }
     },
     components: {
      combine
    },
     computed: {
     	formList: function () {
     		let dates = this.initDate(true);
            return [
               {
                 type: 'timer',
                 options: {
                   key: 'searchDate',
                   value: {startDate: dates.start, endDate: dates.end},
                   width: '23em',
                   fontSize: '0.8em'
                 }
               },
               {
                 type: 'combineSelect',
                 options: {
                    inputOpt: {
                       width: '100%',
                       type: 'typeSearch',
                       hideSelect: false,
                       selectOpt: {
                           width: '7.3em',
                           cs: 'client',
                           data: [
                               {text: '人员姓名', value: 'userName'}
                           ],
                           key: 'searchType'
                       }
                   },
                   autoSearch: true,
                   url: 'replenish/queryUser',
                   method: 'post',
                   paramType: 'form'
                 }
               },
               {
                 type: 'input',
                 options: {
                   type: 'typeSearch',
                   key: 'searchParam',
                   selectOpt: {
                     width: '7em',
                     cs: 'client',
                     data: [
                      // {text: '操作人员', value: 'operator'},
                      {text: '操作内容', value: 'content'}
                     ]
                   }
                 }
               }
            ]
     	}
     },
     created: function () {
        let dates = this.initDate(true);
        this.tableOpt.queryParam['startDate'] = this.searchParam.startDate = dates.start;
        this.tableOpt.queryParam['endDate'] = this.searchParam.endDate = dates.end;
        this.tableShow = true;
     },
     methods: {
       getOperator: function (val) {
           if (val !== ''  && val.userId !== null && val.userId !== '' && val.userId != this.searchParam['userId']) {
              this.searchParam['userId'] = val.userId;
              this.getParams(1);
           
           } else if(val === ''  && this.searchParam['userId'] !== '') {
              this.searchParam['userId'] = '';
              this.getParams(1);
         
           }
          
       },
     	getFrom: function (val) {
     	  if (val.combine) {
              if ( this.searchParam[val.key.split('.')[1]] !== val.val) {
                this.searchParam = this.cleanSearchData(this.searchParam, ['startDate', 'endDate', 'userId']);
                this.getOption(val, true);
                this.getParams(1);
              }
            } else {
              this.getOption(val, true);
            }
     	},
     	getParams: function (pageNumber) {
          let arr = this.getSearchData(this.searchParam);
          this.getHeight();
          this.searchTable(arr, pageNumber);
     	},
      windowChange:function () {
         this.getHeight();
      }
     },
     mounted: function () {
        let that = this;
        /*window.onresize = function () {
          that.getHeight();
        }*/
     },
     watch: {
     	searchDate(val) {
     	  let arr = val.split('至');
     	  this.searchParam.startDate = arr[0];
     	  this.searchParam.endDate = arr[1];
     	  this.getParams(1);
     	}
     }
    }
</script>

<style lang="scss">
</style>


