<template >
    <div class="mySelect combineSelect" :style="{width: width}">
        <inputs ref="input" :style="{width: option.inputOpt.width || width}" :option="option.inputOpt"  @cleanInpt="getInputData" @RealData = "getInputData" @onFocus="getInputData" @getInput="hideSelect"></inputs>
        <ul  class="selectUl combineUl" v-show="showSelect" ref="selections" :style="{'width': 'auto','min-width':'100%', right: option.position === 'right' ? '0' : 'auto'}">
            <template v-if="datas.length == 0"><li style="text-align:center;" :style="{width: 'auto','min-width': 'calc(100% - 2em)'}"><p>没有匹配数据，请重新输入</p></li></template>
            <template  v-if="datas.length != 0" ><li v-for="(item, $index) in datas"  :style="{width: 'max-content!important'}" style="overflow-x: auto;width:auto" :class="{'liSelect': index == $index}" :value="item.value" @mousedown="selectChange(item, $index)">{{item.text}}</li></template>
        </ul>
    </div>
</template>
<script>
    export default {
        data () {
            return {
                searchData: {},
                datas: [],
                index: -1,
                showSelect: false,
                ulWidth: 'auto',
                lastData : {},
                currentSelect: ''
            }
        },
        props: {
            option: {
                type: Object,
                'default'() {
                    return {
                        inputOpt: {
                            width: '20em',
                            type: 'typeSearch',
                            key: 'searchData',
                            selectOpt: {
                                width: '7em',
                                cs: 'client',
                                data: [
                                    {text: '用户表号', value: 'deviceNo'},
                                    {text: '用户姓名', value: 'customerName'},
                                    {text: '联系电话', value: 'customerContact'},
                                    {text: '用户地址', value: 'customerAddr'}
                                ],
                                text: 'text',
                                value: 'value',
                                key: 'searchType'
                            }
                        },
                        url: 'customerRecord/query'
                    }

                }
            },
            width: {
                type: String,
                'default': '30em'
            },
            queryParam: {
               type: Object
            }
        },
        methods: {
            hideSelect: function (val) {

                this.showSelect = false;
                if(typeof val.val === 'undefined' || val.val === '') {
                      this.$emit('getSelects', '');
                }
            },
            
            getInputData: function (val) {
                let value = val.val;
                let flag = false;
                for (let item in this.searchData) {
                    if (item == val.key && this.searchData[item] == value) {
                      flag = true;
                      break;
                    }
                }

                this.searchData = {};
                this.searchData[val.key] = value;
                if (!flag && typeof value !== 'undefined' &&  value !== '' && (this.currentSelect == '' ||  this.currentSelect != '' &&  value.toString().replace(/\s/g, "") != this.currentSelect[this.option.inputOpt.selectOpt.text || 'text'].replace(/\s/g, ""))){
                  this.getSelect();
                }
            },
            getSelect: function () {
                let that = this;
                that.datas = [];
                let params =  this.searchData;
                  for (let item in this.queryParam) {
                    params[item] = this.queryParam[item];
                  }
                  params.pageSize = 50;
                  params.pageNumber = 1;
                  let sign = that.option.paramType === 'form' ? true : false;
                  that.getRequest({
                    url: that.option.url,
                    param: params,
                    method: that.option.method || 'post',
                    success: function (data) {
                      let res = data.data;
                     
                      if (res.length > 0) {
                        that.datas = res;
                        that.autoSelect();
                        that.$nextTick(function () {
                          that.showSelect = true;
                          setTimeout(function () {
                            that.ulWidth =  that.ulWidth === 'auto' ? that.$refs.selections.scrollWidth   + 24 + 'px' : that.ulWidth;
                          }, 10)
                        })
                      } else {

                         if(that.option.autoSearch) {
                              that.$emit('getSelects', 'error');
                         }
                       
                      }
                    }
                  }, sign)
            },
            selectChange: function (item , index) {
                this.showSelect = false;
                this.$refs.input.search = item.text;
                this.$emit('getSelects', item);
                this.currentSelect = item;
                this.ulWidth = 'auto'
            },
            autoSelect: function () {
                if (this.datas.length ===1) {
                   this.$emit('getSelects', this.datas[0]);
                   this.currentSelect = this.datas[0];
                   this.ulWidth = 'auto'
                   this.selectChange(this.datas[0] , 0);
                }
            }
        },
        mounted: function () {
        },
        computed: {

        }
    }
</script>
<style lang="scss">
    @import "../../assets/main.scss";
    .combineSelect{
        .input-container{
            .selectIput{
                border-top-right-radius: 5px;
                border-bottom-right-radius: 5px;
                padding-right:30px
            }
            span.glyphicon-remove{
                margin-right:0!important;
            }
            span.glyphicon-remove + button {
                display: none
            }
        }
    }
    .mySelect {
        float:left;
        position:relative;
        width:200px;
    }
    .selectUl{
        width:100%;
        max-height: 200px;
        overflow-y: auto;
        overflow-x: hidden;
        border:1px solid lightgray;
        border-bottom-left-radius: 4px;
        border-bottom-right-radius: 4px;
        position:absolute;
        z-index:2;
        //background:white;
        //color: black;
        color:white;
        background: #305ea0;
        border:1px solid rgba(255,255,255,0.3);

    }
    .selectUl:after{
        content:'';
        width:1px;
        height:0;
        clear:both
    }
    .selectUl li {

        padding:6px 12px!important;
        margin:0.5px 0;
        cursor:pointer;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        text-align:left;
        float:left;
    }
    .selectUl li:hover,  .selectUl li.liSelect{
       // background: #4C5E76;
        color:white;
        background: #227fdf;
    }
    .selectBtn{
        width:100%;
        position:relative;
     //   padding: 6px 12px;
        text-align:left;
        outline: none!important;
       // height:34px;
    }
    .selectBtn span{
        right: 10px;
        position: absolute;
        //top: 13px;
        left:2px
    }
    .combineUl{
        top: 42px;
        z-index: 99!important;
        min-width: 200px;
        overflow: auto!important;
        min-height:50px
    }
</style>



